import React from 'react'
import {Route, withRouter, HashRouter} from "react-router-dom";
import {Provider, connect} from "react-redux"
import {compose} from "redux";
import store from "./Redux/radux-store";
import Start from "./components/Start/Start";
import Footer from "./components/Footer/Footer";
import {Header} from "./components/Header/Header";
import NavBar from "./components/NavBar/NavBar";
import s from "./App.module.css"
import Profile from "./components/Profile/Profile";
import Chat from "./components/Chat/Chat";
import Friends from "./components/Friends/Friends";
import Settings from "./components/Settings/Settings";


function App(props) {
  return <div className={s.AppWrapper}>
    <div className={s.one}>
      <Header/>
    </div>
    <div className={s.two}>
      <NavBar/>
    </div>
    <div className={s.three}>
      <switch>
        <Route exact path={"/home"} render={() => <Start/>}/>
        <Route exact path={"/profile"} render={() => <Profile/>}/>
        <Route exact path={"/dialogs"} render={() => <Chat/>}/>
        <Route exact path={"/friends"} render={() => <Friends/>}/>
        <Route exact path={"/settings"} render={() => <Settings/>}/>
      </switch>
    </div>
    <div className={s.four}>
      <Footer/>
    </div>
  </div>
}

let mapStateToProps = (state) => {
  return {
  }
}


let AppContainer = compose(withRouter, connect(mapStateToProps, {}))(App);

const MainApp = (props) => {
  return <HashRouter>
    <Provider store={store}>
      <AppContainer/>
    </Provider>
  </HashRouter>
}

export default MainApp
