import React from "react";
import s from "./Settings.module.css"

export default function Settings(props){
    return<div className={s.content}>
        <div className={s.info}>Some info</div>
        <div className={s.box1}>Privat</div>
        <div className={s.box2}>Friends</div>
        <div className={s.box3}>Videos</div>
        <div className={s.box4}>Stream</div>
        <div className={s.box5}>Profile</div>
        <div className={s.box6}>All settings</div>
    </div>
}